<template>
  <div style="margin-top: 5px;">
    <!-- 此处是面包屑 -->

    <div style="float: left;">

      <el-breadcrumb style="padding: 5px;" separator="/">
        <el-breadcrumb-item :to="{ path: '/storage' }">全部文件</el-breadcrumb-item>
        <el-breadcrumb-item v-for="(item,index) in list" :to="{path:'/storage?parentName='+pathList[index]+''}">{{item}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-dialog title="" :visible.sync="perentDialog" width="30%" center>
      <el-progress :percentage="perent" :format="format"></el-progress>
    </el-dialog>
    <div style="margin-bottom: 10px;float: right;margin-right: 1.25rem;display: -webkit-box;">

      <el-upload style="margin-right: 10px;" class="upload-demo" :with-credentials="true" :data="form" name="upload"
        ref="upload" action="/api/disk/uploadFile" :before-upload="checkMd5" :on-progress="progress" :on-change="On_change_Upload"
        :on-success="success" :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary">上传文件</el-button>
      </el-upload>
      <!-- <div class="createFolder"> -->
      <el-button @click="test" type="primary" size="small">创建文件夹</el-button>


    </div>
  </div>

</template>
<script>
  export default {
    inject: ['reload'],
    data() {
      return {
        userName: '',
        parentName: this.$route.query.parentName,
        uploadDialog: false,
        backButton: true,
        perent: 0,
        form: {
          parentName: ''
        },
        magnet: {
          dialog: false
        },
        activceList: [],
        stoppedList: [],
        list: [],
        pathList: [],
        imgUrl: require("../assets/logo_1.png"),
        perentDialog: false
      }
    },
    computed: {
      // 这里定义上传文件时携带的参数，即表单数据
      upData: function() {
        return {
          body: this.form
        }
      }

    },
    mounted() {
      if (this.parentName == null || this.parentName == "undefined") {
        this.parentName = "/";
      }
      this.list = this.parentName.split("/").filter(item => item != '');
      this.pathList[0] = "/" + this.list[0] + "/";
      for (var i = 1; i < this.list.length; i++) {
        this.pathList[i] = this.pathList[i - 1] + this.list[i] + "/";
      }

    },
    methods: {
      format(percentage) {
        return "";
      },
      progress(event, file, fileList) {
        this.perent = file.percentage;
        console.log(event);
        console.log(file);
      },
      On_change_Upload(file) {
        this.form.parentName = this.parentName;
        this.$refs.upload.submit();
        this.perentDialog = true;
      },
      success() {
        this.perentDialog = false;
        this.reload();
      },
      checkMd5(file) {
        console.log(file);
      },
      handleClose() {},
      test() {
        this.$prompt('请输入文件夹名', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^((?!\\|\/|:|\*|\?|<|>|\||'|%).){1,8}$/,
          inputErrorMessage: '文件夹名格式不正确'
        }).then(({
          value
        }) => {
          var name;

          name = this.parentName;
          this.$axios.post('/api/disk/createFolder', {
            selfName: value,
            parentName: name
          }).then(responese => {
            if (responese.data.code === 20000) {
              this.insertFresh();
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      }
    }
  }
</script>

<style>
  /* .el-upload-Dialog,
  .createFolder,
  .download,
  .share,
  .magnet {
    text-align: left !important;
    margin: 10px !important;
    float: left;
  }

  .back {
    text-align: left;
    margin: 10px;
  } */
  .header button {
    text-align: left;
  }
</style>



