<template>
  <div>
    <el-form ref="form" :model="form">
      <el-form-item label="服务器ip">
        <el-input v-model="form.ip"></el-input>
      </el-form-item>
      <el-form-item label="数据库用户名">
        <el-input v-model="form.user"></el-input>
      </el-form-item>
      <el-form-item label="数据库密码">
        <el-input v-model="form.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="post">应用</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          ip: '',
          user: '',
          password: ''
        }
      }
    },
    mounted() {
      this.get();
    },
    methods: {
      save() {

      },
      get() {
        this.$axios.get('/api/v1/admin/config').then(responese => {
          this.form = responese.data.result;
        });
      },
      post() {
        this.$axios.post('/api/v1/admin/AlterConfig',
          this.form
        ).then(responese => {
          console.log(responese);
        });
      }
    }
  }
</script>

<style>
</style>



