<template>
  <div>
    <el-menu class="el-menu-demo" mode="horizontal">
      <el-menu-item index="1">
        <el-button icon="el-icon-plus" size="mini" round="" @click="show">增加</el-button>
      </el-menu-item>
    </el-menu>
    <el-table :data="users">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column label="序列号">
        <template slot-scope="scope">
          <a style="color: #000000;">{{scope.row.uid}}</a>
        </template>
      </el-table-column>
      <el-table-column label="邮箱">
        <template slot-scope="scope">
          <a style="color: #000000;">{{scope.row.email}}</a>
        </template>
      </el-table-column>
      <el-table-column label="角色名">
        <template slot-scope="scope">
          <a style="color: #000000;">{{scope.row.roleName}}</a>
        </template>
      </el-table-column>
      <el-table-column label="用户名">
        <template slot-scope="scope">
          <a style="color: #000000;">{{scope.row.name}}</a>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot="header" slot-scope="scope">
          <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
        </template>
        <template slot-scope="scope">
          <!-- <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">编辑</el-button> -->
          <el-dropdown size='mini'>
            <el-button round>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button type="text" size="mini">详情</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button type="text" size="mini">停用</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button type="text" size="mini">修改</el-button>
              </el-dropdown-item>
            </el-dropdown-menu>

          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="新增用户" :visible.sync="addDialog">
      <el-form :model="form">
        <el-form-item label="邮箱">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="昵称">
          <el-input v-model="form.nickName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.rid" placeholder="角色">
            <el-option v-for="role in roles" :label='role.name' :value="role.rid"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary" @click="add">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        users: [],
        search: '',
        addDialog: false,
        form: {
          email: '',
          rid: '',
          password: '',
          nickName: ''
        },
        roles: []
      }
    },
    mounted() {
      this.get();
    },
    methods: {
      get() {
        this.$axios.get('/api/v1/admin/users').then(responese => {
          this.users = responese.data.result;
        })
      },
      getRoles() {
        this.$axios.get('/api/v1/admin/getRoles').then(responese => {
          this.roles = responese.data.result;
        })
      },
      show() {
        this.addDialog = true;
        this.getRoles();
      },
      add() {
        this.$axios.post('/api/v1/admin/addUser', this.form).then(responese => {
          var msg='';
          if (responese.data.result == true) {
            msg="添加成功"
          }else{
            msg="添加失败"
          }
          this.$message({
              message: msg,
              type: 'success'
            });
        });
      },
      update() {

      },
      search() {

      },
      detail() {

      },
      stop() {

      }
    }
  }
</script>

<style>
</style>



