<template>
  <div>
    <div style="margin-bottom: 50px;">
      <el-row>
        <el-col v-for="disk in DiskList" :span="8" style="padding: 5px;">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>真实映射:{{disk.id}}</span>
              <el-button style="float: right; padding: 3px 0" type="text">详情</el-button>
            </div>
            <div>
              {{disk.folderName}} <el-divider></el-divider>
              {{disk.customName}}  <el-divider></el-divider>
              {{disk.deviceName}}

            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <div>
      <el-form ref="form" :model="DiskForm">
        <el-form-item label="文件夹名">
          <el-input v-model="DiskForm.folderName"></el-input>
        </el-form-item>
        <el-form-item label="设备名称">
          <el-input v-model="DiskForm.deviceName"></el-input>
        </el-form-item>
        <el-form-item label="相对访问路径">
          <el-input v-model="DiskForm.customName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="add">添加</el-button>
              <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        DiskList: [],
        DiskForm:{
          folderName:'',
          deviceName:'',
          customName:''
        }
      }
    },
    mounted() {
      this.get();
    },
    methods: {
      get() {
        this.$axios.get('/api/v1/admin/hard_Devices').then(responese => {
          this.DiskList = (responese.data.result);
        });

      },
      add() {
		this.$axios.post('/api/v1/admin/insertHardDevices',this.DiskForm).then(responese => {
          console.log(responese.data.result);
        });
      },
      detail() {

      }
    }
  }
</script>

<style>
</style>



