<template>
  <div>
    <div style="margin-bottom: 50px;">
      <el-row>
        <el-col v-for="pattern in patternList" :span="8" style="padding: 5px;">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>{{pattern.PatternPath}} {{pattern.PatternName}}</span>
              <el-button style="float: right; padding: 3px 0" type="text">详情</el-button>
            </div>
            <div>
              容量: {{pattern.total}}   <el-divider></el-divider>
              已使用: {{pattern.used}}  <el-divider></el-divider>
              空闲: {{pattern.free}}    <el-divider></el-divider>
              百分比: {{pattern.bfb}}
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        patternList: [],
      }
    },
    mounted() {
      this.get();
    },
    methods: {
      get() {
        this.$axios.get('/api/v1/admin/PatternData').then(responese => {
           this.patternList = (responese.data.result);
          console.log(responese);
        });

      },
      add() {

      },
      detail() {

      }
    }
  }
</script>

<style>
</style>



